import React, {Component} from 'react'
import {withRouter} from 'react-router'
import {Layout} from 'antd'
import style from './style/index.scss'
import {SeverModule} from './components'

const {Content} = Layout
const baseStyle = {
  margin: '30px 0'
}

const selectDate = [
  {text: '今日', type: 'day'},
  {text: '本周', type: 'week'},
  {text: '本月', type: 'month'},
  {text: '全年', type: 'year'}
]

class Adpp extends Component {
  constructor() {
    super()

    this.state = {
      dataType: 'xiaoshou',
      dateTimetype: 'day'
    }
  }

  render() {
    const {dataType, dateTimetype} = this.state
    return (
      <section>
        <Content style={baseStyle}>
          <SeverModule />
          <div className={style.toolBarContent}>
            <div className={style.toolBar}>
              <div className={style.fl}>
                <span
                  className={dataType === 'xiaoshou' ? style.toolBarTabSelected : style.toolBarTab}
                  style={{borderRight: '1px solid #e4e8f7'}}
                  onClick={() => this.setState({dataType: 'xiaoshou'})}>销售额</span>
                <span
                  className={dataType === 'fangwen' ? style.toolBarTabSelected : style.toolBarTab}
                  onClick={() => this.setState({dataType: 'fangwen'})}>访问量</span>
              </div>
              <div className={style.fr}>
                {
                  selectDate.map((o, i) =>
                    <span
                      key={i}
                      className={dateTimetype === o.type ? [style.dateItem, style.blueColor].join(' ') : style.dateItem}
                      onClick={() => this.setState({dateTimetype: o.type})}
                    >{o.text}</span>)
                }
              </div>
            </div>
            <div>
              <img src="/image/echart.png" alt="" className={style.imgStyle}/>
              <div className={style.dataModule}>
                <div className={style.dataTitle}>门店销售排行榜</div>
                {
                  [1, 2, 3, 4, 5, 6, 7].map((o, i) =>
                    <div className={style.listItem} key={i}>
                      <span className={i < 3 ? [style.circle, style.circleYellow].join(' ') : style.circle}>{o}</span><span>静安路1号店</span>
                      <span className={style.dataItemNum}>2312312</span>
                    </div>
                  )
                }
              </div>
            </div>
          </div>
        </Content>
      </section>
    )
  }
}

export default withRouter(Adpp)



